
import React, { useState, useEffect } from 'react';
import { saveAs } from 'file-saver';

function Synopsis({ claimNumber }) {
  const [componentData, setComponentData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [handleButtonClickDisabled, setHandleButtonClickDisabled] = useState(false);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const backendURL = `${window.location.protocol}//${window.location.hostname}:${window.location.port}/claim/loss/${claimNumber}`;
        const response = await fetch(backendURL);
        if (response.ok) {
          const data = await response.json();
          if (Array.isArray(data) && data.length > 0) {
            const claim = data[0];
            // Convert date strings to Date objects
            claim.dateOfLoss = new Date(claim.dateOfLoss);
            claim.dateOfReport = new Date(claim.dateOfReport);
            claim.effectiveDate = new Date(claim.effectiveDate);
            claim.expirationDate = new Date(claim.expirationDate);
            claim.cancellationDate = new Date(claim.cancellationDate);

            // ...
            setComponentData(claim);
          }
        } else {
          throw new Error('Failed to fetch data');
        }
        setLoading(false);
      } catch (error) {
        console.error('Error fetching data:', error);
      } finally {
        setLoading(false);
      }
    };
    fetchData();
  }, [claimNumber]);

  const backendURL = `${window.location.protocol}//${window.location.hostname}:${window.location.port}`;

  const handleCsvDownload = async () => {
    try {
      const response = await fetch(`${backendURL}/download-csv?claimNumber=${claimNumber}`);
      if (response.ok) {
        const blob = await response.blob();
        const currentDate = new Date();
        const year = currentDate.getFullYear();
        const month = String(currentDate.getMonth() + 1).padStart(2, '0');
        const day = String(currentDate.getDate()).padStart(2, '0');
        const hours = String(currentDate.getHours()).padStart(2, '0');
        const minutes = String(currentDate.getMinutes()).padStart(2, '0');
        const seconds = String(currentDate.getSeconds()).padStart(2, '0');

        const timestamp = `NXT-${year}-${month}-${day} ${hours}-${minutes}-${seconds}`;
        const filename = `${timestamp}.csv`;

        saveAs(blob, filename);
        alert('CSV downloaded successfully!');
      } else {
        console.error('Failed to download CSV');
      }
    } catch (error) {
      console.error('Error downloading CSV:', error);
    }
  };

  const handleButtonClick = async () => {
    if (!handleButtonClickDisabled) {
      try {
        setHandleButtonClickDisabled(true);
      const backendURL = `${window.location.protocol}//${window.location.hostname}:${window.location.port}/generateClaimFile`;
      const response = await fetch(backendURL, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            claimNumber: claimNumber,
            policyNumber: componentData?.policyNumber || '',
          }),
        });
        if (response.ok) {
          console.log('Request sent successfully!');
          // Show an alert or popup
          window.alert('Your claim has been sent to Guidewire and is under processing.');
        } else {
          console.error('Failed to send request');
        }
      } catch (error) {
        console.error('Error sending request:', error);
      } finally {
        // Reset the button disabled state after the request is done (success or error)
        setHandleButtonClickDisabled(false);
      }
    }
  };

  if (loading) {
    return <div>Loading...</div>;
  }

  return (
    <div className="ms-3">
      <h5>Claim Synopsis</h5>
      <hr />  
      <div className="row">  
      <div className="row mb-2">
          <div className="col-4">
            <label htmlFor="claimNumber">Claim Number</label>
          </div>
          <div className="col-5">
            <input
              id="claimNumber"
              value={claimNumber}
              readOnly
              type="text"
              className="w-100 form-control"
            />
          </div>
        </div> 
        <div className="row mb-2">  
          <div className="col-4">  
            <label htmlFor="policyNumber">Policy Number</label>  
          </div>  
          <div className="col-5">  
            <input  
              id="policyNumber"  
              value={componentData?.policyNumber || ''}  
              readOnly  
              type="number"  
              className="w-100 form-control"  
              maxLength={25}  
            />  
          </div>  
        </div>  
        <div className="row mb-2">  
          <div className="col-4">  
            <label htmlFor="name">Name</label>  
            <i className="text-danger h5">*</i>  
          </div>  
          <div className="col-5">  
            <input  
              id="name"  
              value={componentData?.name || ''}  
              readOnly  
              type="text"  
              className="w-100 form-control"  
            />  
          </div>  
        </div>  
        <div className="row mb-2">  
          <div className="col-4">  
            <label htmlFor="dateOfLoss">Date Of Loss</label>  
          </div>  
          <div className="col-5">  
            <input  
              id="dateOfLoss"  
              type="date"  
              value={componentData?.dateOfLoss?.toISOString().split('T')[0] || ''}  
              readOnly  
              className="w-100 form-control"  
            />  
          </div>  
        </div>  
        <div className="row mb-2">  
          <div className="col-4">  
            <label htmlFor="adjuster">Adjuster</label>  
          </div>  
          <div className="col-5">  
            <input  
              id="adjuster"  
              type="text"  
              value={componentData?.adjuster || ''}  
              readOnly  
              className="w-100 form-control"  
            />  
          </div>  
        </div>  
        <div className="row mb-2">  
          <div className="col-4">  
            <label htmlFor="dateOfReport">Date Reported</label>  
          </div>  
          <div className="col-5">  
            <input  
              type="date"  
              id="dateOfReport"  
              value={componentData?.dateOfReport?.toISOString().split('T')[0] || ''}  
              readOnly  
              className="w-100 form-control"  
            />  
          </div>  
        </div>  
        <div className="row mb-2">  
          <div className="col-4">  
            <label htmlFor="address1">Loss Location</label>  
          </div>  
          <div className="col-5">  
            <input  
              id="address1"  
              type="text"  
              value={componentData?.address1 || ''}  
              readOnly  
              className="w-100 form-control"  
            />  
          </div>  
        </div>  
        <div className="row mb-2">  
          <div className="col-4">  
            <label htmlFor="lossDescription">Loss Description</label>  
          </div>  
          <div className="col-5">  
            <input  
              type="text"  
              className="w-100 form-control"  
              maxLength={500}  
              id="lossDescription"  
              value={componentData?.lossDescription || ''}  
              readOnly  
            />  
          </div>  
        </div>  

      <div className="row mb-2">
        <div className="col-4">
          <label htmlFor="lossTime">Time Of Loss</label>
          <i className="text-danger h5">*</i>
        </div>
        <div className="col-5">
          <input
            id="lossTime"
            type="time"
            value={componentData?.timeOfLoss|| ''}
            className="w-100 form-control"
          />
        </div>
      </div>

      <div className="row mb-2">
        <div className="col-4">
          <label htmlFor="reportedBy">Reported By</label>
          <i className="text-danger h5">*</i>
        </div>
        <div className="col-5">
          <input
            id="reportedBy"
            type="text"
            value={componentData?.reportedBy || ''}
            className="w-100 form-control"
          />
        </div>
      </div>

      <div className="row mb-2"> 
          <div className="col-4"> 
            <label htmlFor="policyType">Policy Type</label> 
          </div> 
          <div className="col-5"> 
          <input
            className="w-100 form-control" 
            id="policyType" 
            value={componentData?.policyType || ''}
          />
        </div>
      </div>  
      <div className="row mb-2"> 
          <div className="col-4"> 
            <label htmlFor="effectiveDate">Effective Date</label> 
            <i className="text-danger h5">*</i> 
          </div> 
          <div className="col-5"> 
          {/* <DatePicker
      className='w-100 form-control'
        selected={effectiveDate}
         id="effectiveDate"
        onChange={(e)=>handleInputChange(e,'effectiveDate')}
        dateFormat="MM-dd-yyyy"
        placeholderText="MM-DD-YYYY"
      /> */}
<input id="effectiveDate" type="Date" value={componentData?.effectiveDate?.toISOString().split('T')[0] || ''}  
     className='w-100 form-control' /> 
             
          </div> 
        </div> 
 
        <div className="row mb-2"> 
          <div className="col-4"> 
            <label htmlFor="expirationDate">Expiration Date</label> 
            <i className="text-danger h5">*</i> 
          </div> 
          <div className="col-5"> 
          {/* <DatePicker
              className="w-100 form-control"
              selected={expirationDate}
              id="expirationDate"
             onChange={(e)=>handleInputChange(e,'expirationDate')}
             dateFormat="MM-dd-yyyy"
             placeholderText="MM-DD-YYYY"
           /> */}
           <input id="expirationDate" type="Date" value={componentData?.expirationDate?.toISOString().split('T')[0] || ''}  
     className='w-100 form-control' /> 
            
              
          </div> 
        </div> 
 
        <div className="row mb-2"> 
          <div className="col-4"> 
            <label htmlFor="cancellationDate">Cancellation Date</label> 
          </div> 
          <div className="col-5"> 
          {/* <DatePicker
      className='w-100 form-control'
        selected={cancellationDate}
         id="cancellationDate"
        onChange={(e)=>handleInputChange(e,'cancellationDate')}
        dateFormat="MM-dd-yyyy"
        placeholderText="MM-DD-YYYY"
      /> */}
       <input id="cancellationDate" type="Date" value={componentData?.cancellationDate?.toISOString().split('T')[0] || ''}  
     className='w-100 form-control' />    
          </div> 
        </div>  

        <div className="row mb-2">
          <div className="col-4">
            <label htmlFor="lossCause">Loss Cause</label>
          </div>
          <div className="col-5">
            <input
              className="w-100 form-control"
              id="lossCause"
              value={componentData?.lossCause || ''}
            />	
          </div>
        </div>

        {/* <div className="row mb-2">
          <div className="col-4">
            <label htmlFor="otherDescription">Other Description</label>
          </div>
          <div className="col-8">
            <input
              type="text"
              className="w-100 form-control"
              maxLength={500}
              id="otherDescription"
              value={otherDescription}
              onChange={handleInputChange}
            />
          </div>
        </div> */}

        <div className="row mb-2">
          <div className="col-4">
            <label htmlFor="typeOfLoss">Type Of Loss</label>
          </div>
          <div className="col-5">
            <input
              className="w-100 form-control"
              id="typeOfLoss"
              value={componentData?.typeOfLoss || ''}
            />
          </div>
        </div>
        <div className="row mb-2">
          <div className="col-4">
            <label htmlFor="lossAddress">Address</label>
            </div>
          <div className="col-5">
            <input
              type="text"
              className="w-100 form-control"
              maxLength={500}
              id="lossAddress"
              value={`${componentData?.address || ''}, ${componentData?.city || ''},${componentData?.state || ''}, ${componentData?.countries || ''}, ${componentData?.zipcode || ''}`}
            />
          </div>
        </div>
        <div className="row mb-2">
        <div className="col-4">
          <label htmlFor="lossParty">Loss party</label>
        </div>
        <div className="col-5">
          <input
          type="text"
          className="w-100 form-control"
            id="lossParty"
            value={componentData?.lossParty || ''}
          />
        </div>
      </div>

      <div className="row mb-2">
        <div className="col-4">
          <label htmlFor="primaryCoverage">Primary Coverage</label>
        </div>
        <div className="col-5">
          <input
          type="text"
          className="w-100 form-control"
            id="primaryCoverage"
            value={componentData?.primaryCoverage || ''}
          />
        </div>
      </div>
      <div className="row mb-2">
        <div className="col-4">
          <label htmlFor="status">Exposure Status</label>
        </div>
        <div className="col-5">
          <input
          type="text"
          className="w-100 form-control"
            id="status"
            value={componentData?.exposuresStatus || ''}
          />
        </div>
      </div>


        <div>
        <button onClick={handleButtonClick} disabled={handleButtonClickDisabled} className="btn btn-primary mb-2">
        Send Claim to Guidewire
      </button>
      </div>
      <div>
        
        <button onClick={
          handleCsvDownload} className="btn btn-primary mb-2">Download CSV</button>
      </div>
        {/* <div>  
          <Upcomingactivities claimNumber={claimNumber} />  
        </div>   */}
        {/* <div>  
          <h2>Recent Notes</h2>  
        </div>   */}
      </div>  
    </div>  
  );  
};
  
export default Synopsis;

